import { Link, useParams } from "react-router-dom";
import { useState, useEffect } from "react";
import MarvelService from "../../services/MarvelService";
import ErrorMessage from "../error/Error";

const SingleComics = () => {
  const { id } = useParams();
  const [comics, setComics] = useState(null);
  const { loading, error, getComics, clearError } = MarvelService();
  useEffect(() => {
    updateComics();
  }, [id]);
  const updateComics = () => {
    if (!id) {
      return;
    }
    clearError();
    getComics(id).then(onComicsLoaded);
  };
  const onComicsLoaded = (comics) => {
    setComics(comics);
  };
  console.log(comics);

  const content = comics ? <View comics={comics}></View> : null;
  return <>{content}</>;
};

const View = ({ comics }) => {
  const { id, title, description, pageCount, creators, prices, thumbnail } =
    comics;
  console.log(id);
  creators.items.map((item) => {
    console.log(item.name);
  });

  //   console.log(comics.id);
  //   const { id, title } = comics;
  //   console.log(id);
  return (
    <div className="single-comics">
      <img src={thumbnail.path + "." + thumbnail.extension} alt="" />
      <div className="single-comics-wrapper">
        <div className="single-comics-title">{title}</div>
        <div className="single-comics-desc">
          Description:{" "}
          {description
            ? description
            : "This comics is not really popular, so does not has description"}
        </div>
        <div className="single-comics-desc">Pages: {pageCount}</div>
        <ul className="single-comics-creators">
          Creators:
          {creators.items.map((item) => {
            return <li>{item.name}</li>;
          })}
        </ul>
        <div className="single-comics-price">{prices[0].price} $</div>
        <Link to="/comics">
          <button className="btn">BACK TO LIST</button>
        </Link>
      </div>
    </div>
  );
};
export default SingleComics;
